// Preloader
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-white;
  @include Flex;
  z-index: 9999;
}

.loader{
  width: 200px;
  height: 200px;
  position: absolute;
  top: calc(50% - 100px);
  left: calc(50% - 100px);
  border: 4px solid transparent;
  border-radius: 50%;
  border-bottom: 4px solid $color-sec;
  animation: circle 3s infinite linear;
}

.loader::before{
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  bottom: 10px;
  right: 10px;
  border: 4px solid transparent;
  border-radius: 50%;
  border-bottom: 4px solid $color-prim;
  animation: circle 2s infinite linear;
}

.loader::after{
  content: "";
  position: absolute;
  top: 24px;
  left: 24px;
  bottom: 24px;
  right: 24px;
  border: 4px solid transparent;
  border-radius: 50%;
  border-bottom: 4px solid #818485;
  animation: circle 1s infinite linear;
}

@keyframes circle {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

// Mob Nav
.menuTriger {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $color-prim;
  position: absolute;
  top: 50%;
  transform: translateY(-35%);
  left: 0px;
  @include Flex;
  cursor: pointer;
  z-index: 99;
  display: none;

  html[dir="ltr"] & {
    right: 0;
    left: unset;
  }

  @include Sm {
    display: flex;
  }

  i {
    color: $color-prim;
    font-size: 29px;
  }

  .line{
    width: 25px;
    height: 3px;
    background: $color-white;
    margin: 2px;
    border-radius: 50px;
  }

  .hamburger{
    width: 100%;
    height: 100%;
    @include Flex;
    flex-direction: column;
  }
}

.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba($color-black, 0.2);
  z-index: 999;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  @include Flex;


  .container{
    @include Xs{
      height: 100%;
    }
  }

  .program{
    position: relative;
    background-color: #FFF;
    align-items: initial;
    padding: 40px;
    transform: scale(.9);
    visibility: hidden;
    opacity: 0;
    transition: .5s;

    @include Xs{
      flex-direction: column;
      align-items: center;
      margin-bottom: 0;
      padding: 20px !important;
      margin: 15px 0;
    }

    .popClose{
      font-size: 24px;
      color: #c1c1c1;
      position: absolute;
      top: 18px;
      left: 18px;
      cursor: pointer;

      // html[dir="ltr"] & {
      //   left: unset;
      //   right: 18px;
      // }

      @include mob480{
        top: 12px;
        left: 50%;
        transform: translateX(-50%);
        transition: .5s;

        &:active{
          transform: translateY(3px) translateX(-50%);
        }
      }

      // html[dir="ltr"] & {
      //   left: unset;
      //   right: 18px;
      // }

      &:active{
        transform: translateY(3px);
      }
    }

    .pic{
      min-width: 252px;
      height: 252px;

      @include mob480{
        margin-top: 40px;
      }
    }

    .details{
      padding-right: 35px;

      @include Xs{
        width: auto;
        margin-top: 20px;
        text-align: center;
        padding: 0;
      }

      @include mob600{
        padding: 0 20px;
      }

      html[dir="ltr"] & {
        padding-right: 0;
        padding-left: 35px;

        @include mob375{
          padding-left: 0;
        }
      }
      a{
        font-size: 22px;
        color: #818485;
        font-family: "itBold";
        margin-bottom: 13px;
      }

      & > span{
        font-size: 16px;
        color: rgb(164, 164, 164);
        line-height: 1.875;
        display: block;
        margin-bottom: 25px;

        @include mob600{
          margin-bottom: 15px;
        }
      }
      
      p{
        margin-bottom: 35px;

        @include mob600{
          margin-bottom: 15px;
        }
      }
    }

    .donation{
      margin-bottom: 33px;

      @include mob600{
        margin-bottom: 20px;
      }

      // @include mob480{
      //   flex-direction: column;
      // }

      span{
        color: #818485;
        font-family: "itBold";
        font-size: 18px;

        &:first-child{
          position: relative;

          @include mob480{
            margin-bottom: 10px;
            display: block;
          }

          &:after{
            content: "*";
            color: red;
          }
        }
      }

      input{
        margin: 0 18px 0 12px;
        width: 203px;
        height: 42px;
        border-radius: 100px;
        border: 1px solid #E5E4E2;
        padding: 0 24px;
        color: #a4a4a4;
        background-color: #F6F6F6;
        font-size: 14px;
        transition: .5s;

        &:focus{
          border: 1px solid $color-sec;
        }

        &::placeholder{
          color: #a4a4a4;
        }

        @include mob480{
          margin: 0;
          margin-left: 12px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 12px;
          }
        }
      }
    }

    .actions{
      .cartBtn {
        width: 190px !important;
        flex-grow: unset !important;

        button{
          @include mob600{
            margin-top: 0 !important;
          }
        }

      }

      @include Xs{
        justify-content: center;
      }

      @include mob600{
        align-items: center !important;
        flex-direction: row !important;
      }

      @include mob480{
        flex-direction: column !important;
      }

      @include mob375{
        flex-direction: column !important;
      }
    }

    .amout{

      @include mob600{
        margin-bottom: 0 !important;
      }

      @include mob480{
        margin-left: 0 !important;
        margin-bottom: 10px !important;
      }

      @include mob375{
        margin-bottom: 10px !important;
      }
    }
  }

  &.open {
    opacity: 1;
    visibility: visible;
    
    .program{
      opacity: 1;
      visibility: visible;
      transform: scale(1);
    }
  }
}

.sideNav {  
  position: fixed;
  background: #FFF;
  height: 100vh;
  width: 100%;
  top: 0;
  right: 0;
  flex-direction: column;
  clip-path: circle(0px at 0% 0%);
  @include Flex;
  z-index: 999;
  transition: all .9s ease-out;
  pointer-events: none;
  
  html[dir="ltr"] & {
    clip-path: circle(0px at 100% 0%);
  }

  &.open {
    clip-path: circle(1500px at 0% 0%);
    pointer-events: all;
    
    html[dir="ltr"] & {
      clip-path: circle(1500px at 100% 0%);
    }

    ul {
      opacity: 1;

      li {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;
    position: absolute;
    top: 0;

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #818485;
      width: 30px;
      height: 60px;
      text-transform: uppercase;
      border-radius: 100px;
      // background-color: #818485;
      // border-top-right-radius: 0;
      // border-top-left-radius: 0;
      align-items: flex-end;
      padding-bottom: 10px;
    }

    .close1 {
      display: flex;
      font-size: 18px;
      color: #818485;
      cursor: pointer;
      // width: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      // height: 30px;
      // border-radius: 100px;
      // background-color: #818485;
      // border-bottom-left-radius: 0;
      // border-top-left-radius: 0;
      width: 30px;
      height: 60px;
      border-radius: 100px;
      // background-color: #818485;
      // border-top-right-radius: 0;
      // border-top-left-radius: 0;
      align-items: flex-end;
      padding-bottom: 10px;

      // html[dir="ltr"] & {
      //   border-bottom-left-radius: 100px;
      //   border-top-left-radius: 100px;
      //   border-bottom-right-radius: 0;
      //   border-top-right-radius: 0;
      // }
    }
  }

  & > ul {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    & > li {
      position: relative;
      padding: 10px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(10px);
      opacity: 0;

      &:nth-child(1) {
        transition-delay: 0.3s;
      }
      &:nth-child(2) {
        transition-delay: 0.5s;
      }
      &:nth-child(3) {
        transition-delay: 0.7s;
      }
      &:nth-child(4) {
        transition-delay: 0.9s;
      }
      &:nth-child(5) {
        transition-delay: 1.1s;
      }
      &:nth-child(6) {
        transition-delay: 1.3s;
      }

      a {
        width: 100%;
        display: block;
        font-size: 20px;
        color: #818485;
        transition: 0.5s;
      }

      i {
        position: absolute;
        top: 13px;
        left: 13px;
        transform: rotate(90deg);
        font-size: 9px;
        color: currentColor;
        transition: 0.5s;

        html[dir="ltr"] & {
          left: unset;
          right: 13px;
        }

        &.open {
          transform: rotate(0deg);
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }

  .log {
    @include Flex;
    flex-direction: column;
    // transform: translateY(-60px);

    .icon {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: #f9f9f9;
      @include Flex;

      span {
        font-size: 22px;
        color: #818485;
      }
    }

    .details {
      text-align: center;
      margin-top: 10px;
      display: none;
      p {
        &:first-child {
          color: #818485;
          font-size: 16px;
          font-family: "itBold";
          margin-bottom: 5px;
        }

        &:last-child {
          a {
            font-size: 15px;
            color: #818485;
            font-family: "itLight";
            transition: 0.5s;

            &:hover {
              color: $color-sec;
            }
          }
        }
      }
    }
  }
}

// Start Nav
.nav {
  padding: 15px 0;
  // position: fixed;
  // top: 0;
  // right: 0;
  // width: 100%;
  // z-index: 99;
  background-color: #fff;
  transition: .5s;

  &.scrolled{
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  }

  &.fixed{
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
  }

  &.hidee{
    top: -100%;
  }

  .content {
    @include Flex;
    justify-content: initial;
    position: relative;

    .logo {
      img {
        @include Md {
          width: 250px;
        }

        @include Lg {
          width: auto;
        }

        @include mob480{
          width: 220px;
        }
      }
    }

    .topBar {
      position: absolute;
      left: 0;
      top: 5px;
      display: flex;

      html[dir="ltr"] & {
        left: unset;
        right: 0;
      }

      @include Sm{
        display: none
      }

      .social {
        direction: ltr;

        a {
          font-size: 15px;
          color: #cbcbcb;
          transition: 0.5s;

          &:not(:last-child) {
            margin-right: 23px;

            // html[dir="ltr"] & {
            //   margin-right: 0;
            //   margin-left: 23px;
            // }
          }

          &:hover {
            color: $color-prim;
          }
        }
      }

      .lang {
        @include Flex;
        margin-right: 32px;

        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 32px;
        }

        a {
          text-transform: uppercase;
          font-size: 12px;
          color: #818485;
          transition: .5s;
        }

        svg {
          margin-right: 6px;

          path{
            transition: .5s;
          }

          html[dir="ltr"] & {
            margin-right: unset;
            margin-left: 6px;
          }
        }

        &:hover{
          a{
            color: $color-prim;
          }

          svg{
            path{
              fill: $color-prim;
            }
          }
        }
      }
    }

    .navLinks {
      margin-right: 123px;
      margin-top: 60px;

      @include Md {
        margin-right: 70px;
      }

      @include Lg {
        margin-right: 123px;
      }

      @include Sm{
        display: none
      }

      html[dir="ltr"] & {
        margin-right: 0;
        margin-left: 123px;

        @include Md{
          margin-left: 70px
        }

        @include Lg{
          margin-left: 123px
        }
      }

      ul {
        display: flex;

        li {
          position: relative;
          &:not(:last-child) {
            margin-left: 25px;

            @include Md {
              margin-left: 15px;
            }

            @include Lg {
              margin-left: 25px;
            }

            html[dir="ltr"] & {
              margin-left: 0;
              margin-right: 25px;
            }
          }

          a {
            font-size: 18px;
            font-family: "itMd";
            color: #818485;
            transition: 0.5s;

            @include Md {
              font-size: 16px;
            }

            @include Lg {
              font-size: 18px;
            }

            &:hover {
              color: $color-sec;
            }
          }

          &.active {
            a {
              color: $color-sec;
            }

            &:after {
              content: "";
              position: absolute;
              width: 100%;
              height: 2px;
              background-color: $color-sec;
              top: 100%;
              left: 0;
              transform: translateY(33px);

              @include Md {
                transform: translateY(38px);
              }

              @include Lg {
                transform: translateY(33px);
              }
            }
          }

          &:hover {
            &:after {
              content: "";
              position: absolute;
              width: 100%;
              height: 2px;
              background-color: $color-sec;
              top: 100%;
              left: 0;
              transform: translateY(33px);

              @include Md {
                transform: translateY(38px);
              }

              @include Lg {
                transform: translateY(33px);
              }

              // html[dir="ltr"] & {
              //   @include Md{
              //     transform: translateY(38px);
              //   }

              //   @include Lg{
              //     transform: translateY(33px);
              //   }
              // }
            }
          }
        }
      }
    }

    .options {
      margin-right: auto;
      margin-top: 60px;
      @include Flex;

      @include Sm{
        display: none
      }

      html[dir="ltr"] & {
        margin-right: unset;
        margin-left: auto;
      }

      .log {
        @include Flex;

        .icon {
          width: 48px;
          height: 48px;
          border-radius: 50%;
          background-color: #f9f9f9;
          @include Flex;
          margin-left: 15px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 15px;
          }

          span {
            font-size: 22px;
            color: #818485;
          }
        }

        .details {
          p {
            &:first-child {
              color: #818485;
              font-size: 16px;
              font-family: "itBold";
              margin-bottom: 5px;
            }

            &:last-child {
              a {
                font-size: 15px;
                color: #818485;
                font-family: "itLight";
                transition: 0.5s;

                &:hover {
                  color: $color-sec;
                }
              }
            }
          }
        }
      }
    }

    .myCart {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: $color-prim;
      @include Flex;
      position: relative;
      margin-right: 23px;
      cursor: pointer;

      @include Md {
        margin-right: 15px;
      }

      @include Lg {
        margin-right: 23px;
      }

      html[dir="ltr"] & {
        margin-right: 0;
        margin-left: 23px;
      }

      span {
        font-size: 24px;
        color: #fff;
      }

      p {
        position: absolute;
        top: 0;
        right: 0;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        @include Flex;
        border: 2px solid #fff;
        background-color: $color-sec;
        font-family: "itMd";
        font-size: 11px;
        color: #fff;
        transform: translate(30%, -20%);

        // html[dir="ltr"] & {
        //   right: unset;
        //   left: 0;
        // }
      }
    }

    & > .myCart{
      @include Md{
        display: none;
      }

      margin-right: 0;
      position: absolute;
      left: 50px;
      transform: translateY(5px);
      width: 40px;
      height: 40px;

      html[dir="ltr"] & {
        left: unset;
        right: 50px;
      }
    }
  }
}

