          &:after {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            background-color: $color-sec;
            position: absolute;
            top: 0;
            right: -100%;
            transition: 0.5s;
          }

          &:before {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            background-color: $color-sec;
            position: absolute;
            top: 0;
            left: -100%;
            transition: 0.5s;
          }

          &:hover {
            &:after {
              right: 0%;
            }

            &:before {
              left: 0%;
            }
          }

          span {
            position: relative;
            z-index: 9;
            &:first-child {
              font-size: 19px;
              margin-left: 5px;

              html[dir="ltr"] & {
                margin-left: 0;
                margin-right: 5px;
              }
            }
          }
        }
      }
    }
  }
}

// End Of Banner

