    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 1;
  }

  .fotNav {
    margin-bottom: 45px;

    @include Sm{
      margin-bottom: 35px;
    }

    ul {
      display: flex;

      @include Sm {
        flex-direction: column;
        align-items: center;
      }

      li {
        &:not(:last-child) {
