﻿html{
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: #818485;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $color-prim;
    }

    overflow-y: hidden;

    scrollbar-width: thin;
    scrollbar-color: rgba($color-prim,1) rgba(#818485,1);

    // scroll-behavior: smooth;
}

body{
    font-family: 'it', sans-serif;
    overflow-x: hidden;
    height: auto;
}

*{
    font-family: inherit;
}

input, a, button{
    font-family: 'it', sans-serif;

    &:focus{
        outline: none;
        text-decoration: none;
    }

    &:hover{
        text-decoration: none;
    }
}

button{
    cursor: pointer;

    &:focus, &:active{
        outline: none;
        box-shadow: none;
    }
}

ul.res{
    padding: 0;
    margin: 0;
    list-style: none;
}