  }
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFF;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.loader {
  width: 200px;
  height: 200px;
  position: absolute;
  top: calc(50% - 100px);
  left: calc(50% - 100px);
  border: 4px solid transparent;
  border-radius: 50%;
  border-bottom: 4px solid #E97725;
  animation: circle 3s infinite linear;
}

.loader::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  bottom: 10px;
  right: 10px;
  border: 4px solid transparent;
  border-radius: 50%;
  border-bottom: 4px solid #35C1D5;
  animation: circle 2s infinite linear;
}

.loader::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 24px;
  bottom: 24px;
  right: 24px;
  border: 4px solid transparent;
  border-radius: 50%;
  border-bottom: 4px solid #818485;
  animation: circle 1s infinite linear;
}

@keyframes circle {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

