@mixin Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

@keyframes flash{
    from{
        opacity: .8
    }
    to{
        opacity: 1
    }
}

@keyframes spin{
    0%{
        transform: rotate(0deg)
    }
    100%{
        transform: rotate(360deg)
    }
}